.calculadora{
    padding: 20px;
    height: 570px;
    width: 400px;
    overflow: hidden;
    border-radius: 20px;
    background-color: var(--calc-bg);
    box-shadow: rgba(100, 100, 111,0.2) 0px 7px 29px 0px;

    display: flex;
    flex-direction: column;
    justify-content: flex-end;
}
.calculadora__btns{
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 10px;
}

.calculadora__btns button{
           border: 0;
           outline: 0;
           border-radius: 10px;
           font-size: 1.25rem;
           font-weight: 500;
           background-color: transparent;
           color: var(--text-color);

}   

.calculadora__btns button:last-child{
    border-radius: 50%;
    color: var(--txt-white);
    background-image: linear-gradient(312deg,#cd6e36, #d400ff);
}

.calculadora__btns .btn__op{
    color: var(--calc-btn-color);
}

.calculadora__result{
    position: relative;
    margin-bottom: 20px;
    text-align: right;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    justify-content: flex-end;
}

.calculadora__result__exp{
    font-size: 3.5rem;
    line-height: 1;
    transform-origin: right bottom;
    transition: tranform 0.3 ease;
    color: var(--calc-res-color);
}

.calculadora__result__exp:last-child{
    position: absolute;
    right: 0;
}

.calculadora__result__exp span {
    display: inline-block;
    overflow: hidden;
    transition: width 0.3s ease;
}

@media only screen and (max-width:800px){
  
    .calculadora{
      width: 100%;
      height: 100vh;
      border-radius: 0;
    }
  
  }
  
