@import url('https://fonts.googleapis.com/css2? family= Anton & family= Bebas+Neue & family= Cinzel & family= Road+Rage & family= Roboto+Slab:wght@600; 900 & display=trocar');

:root{
    --body-bg:#f5f5f5;
    --txt-color: #6d6d6d;
    --txt-white: #fff;
    --calc-res-color: #2f2d2f;
    --calc-btn-color:#d400ff;
    --calc-bg: #fff;
}

.dark{
  --calc-res-color: #fff;
  --calc-bg: #373737;

}

*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  -webkit-tap-highlight-color: transparent;
}


body{
  font-family: "Mostserrat", sans-serif;
  font-weight: 400;
  line-height: 1.5;

  background-color: var(--body-bg);
  color: var(--txt-color);

  display: flex;
  justify-content: center;
  padding-top: 50px;
}

button{
  font-family: "Mostserrat", sans-serif;
  cursor: pointer;
}

@media only screen and (max-width:800px){
  
  body{
    display: unset;
    padding-top: 0;
  }

}
